
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
components: {
ViewBox,
XHeader,
Masker,
Tab,
TabItem,
Group,
XInput,
XSwitch,
Cell,
CheckIcon
},
data () {
return {
index: 0,
isConfirm: false,
phone: '',
code: '',
pwd1: '',
pwd2: '',
showClear: false,
time: 60
}
},
mounted () {
},
methods: {
jumpRegisterWeb () {
let self = this
let token = self.$store.state.userInfo.token
let path = self.$store.state.baseUrl + '/app/xsyd/regAgreement.html?' + token
let data = {
action: 'jump_web_show',
path: path,
params: {
name: '用户注册协议'
}
}
self.$store.dispatch('normalRequest', data).then(res => {
})
},
rigister () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/register.do'
let data = {
action: 'init_request',
path: path,
params: {
cellphone: self.phone,
password: self.pwd1,
verifyCode: self.code,
channel: self.$store.state.channel,
imei: self.$store.state.imei
}
}

if (self.isPhoneCorrect(self.phone)) {
if (self.code !== '') {
if (self.mathchPwd(self.pwd1)) {
if (self.pwd1 === self.pwd2) {
this.$vux.loading.show({
text: 'Loading'
})
self.$store.dispatch('normalRequest', data).then(response => {

if (response.response === 'success') {
this.$vux.toast.text('注册成功请登录')
this.phone = ''
this.code = ''
this.pwd1 = ''
this.pwd2 = ''
this.$router.replace('/login')
} else {
this.$vux.toast.text(response.data)
}
this.$vux.loading.hide()
})
} else {
this.$vux.toast.text('两次密码输入不一致')
}
}
} else {
this.$vux.toast.text('验证码不为空')
}
}
},
sendTextCode (type, id) {

this.setBtnDisabled(id)
this.getCode(type)
},
getCode (type) {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/getVerifyCode.do'
let data = {
action: 'init_request',
path: path,
params: {
cellphone: this.phone,
smsType: type,
channel: this.$store.state.channel
}
}
if (self.isPhoneCorrect(self.phone)) {
self.$store.dispatch('normalRequest', data).then(response => {
if (response.response === 'success') {
this.$vux.toast.text('验证码已成功发送')
} else {
this.$vux.toast.text(response.data)
}
})
}
}
}
}



